          <tr>
            <td></td>
            <td>
              <img :src="imgFil(lsMchPhoto.identityBody)" alt="" class="previewImg">
            </td>
          </tr>
          <tr>
            <td></td>
            <td>
              <img :src="imgFil(lsMchPhoto.priAuth)" alt="" class="previewImg">
            </td>
          </tr>
          <tr>
            <td></td>
            <td>
              <img :src="imgFil(lsMchPhoto.cardFace)" alt="" class="previewImg">
            </td>
          </tr>
        </table>


      </div>
    </el-card>


  </div>
</template>

<script>
  import {
    getInfo
  } from '../api/leshua'
  import {url} from '@/utils/request'
  import {mixins} from "../../mixins/mixins";

  let that;
  export default {
    name: "hsf",
    mixins:[mixins],
    props: ['channlename'],
    data() {
      return {
        loading: false, //
        id: '',
        pc: '',
        na: '',
        lsMchInfo: {},
        lsMchPhoto: {},

        industryOptions: [
          {value: '8', name: '//'},
          {value: '9', name: '//'},
          {value: '10', name: '/'},
          {value: '11', name: '//'},
          {value: '12', name: '//'},
          {value: '13', name: '//'},
          {value: '14', name: '/KTV/'},
          {value: '15', name: ''},
          {value: '16', name: ''},
          {value: '17', name: '/'},
          {value: '18', name: '//'},
          {value: '19', name: ''},
          {value: '20', name: ''},
          {value: '21', name: '/'},
          {value: '22', name: ''},
          {value: '23', name: ''},
          {value: '24', name: ''}
        ],//
      }
    },
    beforeCreate: function () {
      that = this
    },
    filters: {
      industryFil: function (val) {
        if (val == '' || val == null) {
          return ''
        }
        let data = that.industryOptions
        let text
        for (let i in data) {
          if (val == data[i].value) {
            text = data[i].name
            break;
          }
        }

        return text || ''
      }
    },
    mounted() {
      this.id = this.$route.query.id;
      this.pc = this.$route.query.pc;
      this.na = this.$route.query.na || '';

      this.getInfofun()

    },
    methods: {
      /**
       * 
       */
      getInfofun: function () {
        getInfo(this.id).then(res => {
          console.log(res);
          let data = res.obj
          this.dataSet(data)
        }).catch(e => {

        })
      },


      /**
       * 
       */
      dataSet: function (data) {
        this.lsMchInfo = data.lsMchInfo
        this.lsMchPhoto = data.lsMchPhoto
      },

      /**
       * 
       */
      jumpOut: function () {
        this.$emit('gobackbutton')
      },

      /**
       *  
       */
      imgFil: function (val) {

        if (val) {
          if (val === '' || val == null) {
            return ''
          }
          let arrayStr = val.split('\\'); // \\
          if(arrayStr.length>4){
            return url + '/filepath/'+arrayStr[(arrayStr.length - 2)]+"/" + arrayStr[(arrayStr.length - 1)];
          }else{
            return url + '/filepath/' + arrayStr[(arrayStr.length - 1)];
          }
        } else {
          let base64 = 'data:image/png;base64,';
          return base64
        }

      },

    }
  }
</script>

<style scoped>

  .info-table {
    width: 100%;
  }

  .info-table tr {
    font-size: 14px;
    color: #3A3A3A;
    line-height: 40px;
  }

  .info-table tr td:nth-child(1) {
    padding-right: 20px;
    text-align: right;
    width: 150px;
  }

  .info-table tr td:nth-child(2) {

  }

  .info-table tr td:nth-child(2) img {
    width: 200px;
    height: auto;
  }


</style>



