import request from '@/utils/request'
import qs from 'qs'

/**
 * 
 * @param merchantId id
 */

//
export function getInfo(id, pc) {
  return request({
    url: '/merchant/third_mch_info/detail',
    method: 'get',
    params: {
      merchantId: id,
      payChannel: pc
    }
  })
}//
export function gethsfInfo(id, pc) {
  return request({
    url: '/merchant/hsf_mch_info/pull_plat_info',
    method: 'get',
    params: {
      merchantId: id,
      payChannel: pc
    }
  })
}



