<script>
  import {
    getFyIncomingInfo
  } from '../api/fy'
  import {url} from '@/utils/request'

  export default {
    name: "hsf",
    props: ['channlename'],
    data() {
      return {
        loading: false, //
        id: '',
        pc: '',
        na: '',
        tableData: {},
				photoData: {}
      }
    },
    mounted() {
      this.id = this.$route.query.id;
      this.pc = this.$route.query.pc;
      this.na = this.$route.query.na || '';

      this.getInfofun()

    },
		filters:{
			licenseTypeFilter(val){
				switch(val){
					case '0':
						return '';
					case '1':
						return '';
					case 'A':
						return '';
					case 'B':
						return '';
					case '2':
						return '';
					default:
						return '';
				}
			},
			dtFilter(val) {
				if(val == null || val == ''){
					return ''
				}
				return val[0] + val[1] + val[2] + val[3] + '-' + val[4] + val[5] + '-' + val[6] + val[7];
			},
			acntTypeFilter(val){
				switch(val){
					case '1':
						return ''
					case '2':
						return ''
					default:
						return '';
				}
			},
			acntArtifFlagFilter(val) {
				switch(val){
					case '0':
						return ''
					case '1':
						return ''
					default:
						return '';
				}
			},
			acntCertifTpFilter(val) {
				switch(val){
					case '0':
						return ''
					default:
						return '';
				}
			},
			wxFlagFilter(val) {
				if(val == '1'){
					return val;
				}
				return '';
			},
			aliFlagFilter(val) {
				if(val == '1'){
					return val;
				}
				return '';
			},
			qpayFlagFilter(val) {
				if(val == '1'){
					return val;
				}
				return '';
			},
			jdpayFlagFilter(val) {
				if(val == '1'){
					return val;
				}
				return '';
			},
			settleTpFilter(val) {
				switch(val){
					case '1':
						return '(T1)';
					case '2':
						return '';
					case '3':
						return '(D1)';
					case '4':
						return '';
					default:
						return '';
				}
			},
			hasReserveAcntFilter(val) {
				if(val == '1'){
					return '';
				}
				return '';
			},
		},
    methods: {
      /**
       * 
       */
      getInfofun: function () {
        getFyIncomingInfo(this.id).then(res => {
          console.log(res);
          this.tableData = res.obj.fuiouMchInfo;
					this.photoData = res.obj.fuiouMchPhoto;
        }).catch(e => {

        })
      },

      /**
       * 
       */
      jumpOut: function () {
        this.$emit('gobackbutton')
      },

      /**
       *  
       */
      imgFil: function (val) {

        if (val) {
          if (val === '' || val == null) {
            return ''
          }
          let arrayStr = val.split('\\'); // \\
          if(arrayStr.length>4){
            return url + '/filepath/'+arrayStr[(arrayStr.length - 2)]+"/" + arrayStr[(arrayStr.length - 1)];
          }else{
            return url + '/filepath/' + arrayStr[(arrayStr.length - 1)];
          }
        } else {
          let base64 = 'data:image/png;base64,';
          return base64
        }

      },

    }
  }
</script>

<style scoped>

