import request from '@/utils/request'
import qs from 'qs'

/**
 * 
 * @param merchantId id
 */

//
export function getInfo(id, pc) {
  return request({
    url: '/merchant/ys_mch_info/find_one',
    method: 'get',
    params: {
      merchantId: id,
    }
  })
}



