import request from '@/utils/request'
import qs from 'qs'

/**
 * 
 * @param merchantId id
 */
/*  2019.8.20~ */

//-
export function getFyIncomingInfo(id) {
  return request({
    url: '/merchant/fuiou_mch_info/find_one',
    method: 'get',
    params: {
      merchantId: id,
    }
  })
}


