<script>
  import {
    getInfo
  } from '../api/ys'
  import {url} from '@/utils/request'
  import {mixins} from "../../mixins/mixins";

  let that;
  export default {
    name: "hsf",
    mixins: [mixins],
    props: ['channlename'],
    data() {
      return {
        loading: false, //
        id: '',
        pc: '',
        na: '',
        mchInfo: {},
        mchPhoto: {},
      }
    },
    beforeCreate: function () {
      that = this
    },
    filters: {
      //
      userTypeFil: function (val) {
        let str = '';
        if (val === '0') {
          str = ''
        } else if (val === '1') {
          str = ''
        } else if (val === '2') {
          str = ''
        } else {
          str = ''
        }
        return str
      },
      //
      balanceAccTypeFil: function (val) {
        let str = '';
        if (val === '11') {
          str = ''
        } else if (val === '21') {
          str = ''
        } else {
          str = ''
        }
        return str
      },

      //
      certTypeFil: function (val) {
        let str = '';
        if (val === '00') {
          str = ''
        } else {
          str = ''
        }
        return str
      },

      //
      mercLvlFil: function (val) {
        let str = '';
        if (val === '1') {
          str = ''
        } else if (val === '2') {
          str = ''
        } else {
          str = ''
        }
        return str
      },

    },
    mounted() {
      this.id = this.$route.query.id;
      this.pc = this.$route.query.pc;
      this.na = this.$route.query.na || '';

      this.getInfofun()

    },
    methods: {
      /**
       * 
       */
      getInfofun: function () {
        getInfo(this.id).then(res => {
          console.log(res);
          let data = res.obj;
          this.dataSet(data)
        }).catch(e => {

        })
      },


      /**
       * 
       */
      dataSet: function (data) {
        this.mchInfo = data.oldYSMchInfo;
        this.mchPhoto = data.oldYSMchPhoto
      },

      /**
       * 
       */
      jumpOut: function () {
        this.$emit('gobackbutton')
      },

      /**
       *  
       */
      imgFil: function (val) {

        if (val) {
          if (val === '' || val == null) {
            return ''
          }
          let arrayStr = val.split('\\'); // \\
          if(arrayStr.length>4){
            return url + '/filepath/'+arrayStr[(arrayStr.length - 2)]+"/" + arrayStr[(arrayStr.length - 1)];
          }else{
            return url + '/filepath/' + arrayStr[(arrayStr.length - 1)];
          }
        } else {
          let base64 = 'data:image/png;base64,';
          return base64
        }

      },

    }
  }
</script>

