            <td>{{yrmMchInfo.industryId|industryFil}}</td>
          </tr>

          <tr v-if="yrmMchInfo.areaType===4 || yrmMchInfo.areaType==5">
            <td></td>
            <td>{{yrmMchInfo.license}}</td>
          </tr>
          <tr v-if="yrmMchInfo.areaType===4 || yrmMchInfo.areaType==5">
            <td></td>
