</template>

<script>
  import {
    getInfo
  } from '../api/tl'
  import {url} from '@/utils/request'
  import {mixins} from "../../mixins/mixins";

  let that;
  export default {
    name: "tl",
    mixins:[mixins],
    props: ['channlename'],
    data() {
      return {
        loading: false, //
        id: '',
        pc: '',
        na: '',
        oldTLMchInfo: {},
        oldTLMchPhoto: {},
      }
    },
    beforeCreate: function () {
      that = this
    },
    filters: {
      industryFil: function (val) {
        if (val == '' || val == null) {
          return ''
        }
        let data = that.industryOptions
        let text
        for (let i in data) {
          if (val == data[i].value) {
            text = data[i].name
            break;
          }
        }

        return text || ''
      }
    },
    mounted() {
      this.id = this.$route.query.id;
      this.pc = this.$route.query.pc;
      this.na = this.$route.query.na || '';

      this.getInfofun()

    },
    methods: {
      /**
       * 
       */
      getInfofun: function () {
        getInfo(this.id).then(res => {
          console.log(res);
          let data = res.obj
          this.dataSet(data)
        }).catch(e => {

        })
      },


      /**
       * 
       */
      dataSet: function (data) {
        this.oldTLMchInfo = data.oldTLMchInfo;
        this.oldTLMchPhoto = data.oldTLMchPhoto;
      },

      /**
       * 
       */
      jumpOut: function () {
        this.$emit('gobackbutton')
      },

      /**
       *  
       */
      imgFil: function (val) {

        if (val) {
          if (val === '' || val == null) {
            return ''
          }
          let arrayStr = val.split('\\'); // \\
          if(arrayStr.length>4){
            return url + '/filepath/'+arrayStr[(arrayStr.length - 2)]+"/" + arrayStr[(arrayStr.length - 1)];
          }else{
            return url + '/filepath/' + arrayStr[(arrayStr.length - 1)];
          }
        } else {
          let base64 = 'data:image/png;base64,';
          return base64
        }

      },

    }
  }
</script>

<style scoped>

  .info-table {
    width: 100%;
  }

  .info-table tr {
    font-size: 14px;
    color: #3A3A3A;
    line-height: 40px;
  }

  .info-table tr td:nth-child(1) {
    padding-right: 20px;
    text-align: right;
    width: 150px;
  }

  .info-table tr td:nth-child(2) {

  }

  .info-table tr td:nth-child(2) img {
    width: 200px;
    height: auto;
  }


</style>



